const mongoose = require('mongoose');
const {
    ObjectId
} = mongoose.Schema;

const {
    Schema
} = mongoose;
const TransChat = new Schema({
    text: {
        type: String,
        required: true
    },
    user: {
        type: ObjectId,
        ref: 'User'
    },
    room: {
        type: ObjectId,
        ref: 'TransRoom'
    },
    sentiment: {
        type: String,

    },
    bot: {
        type: Boolean,
        default: false,
    },
    date: {
        type: Date,
        default: Date.now()
    }
});



const model = mongoose.model('TransChat', TransChat);

export default model;